
.App-header {
  background-color: white;
  min-height: 100vh;
  display: grid;
  flex-direction: column;
  color: white;
}

.about{
  font-size:medium;
}
.authorName {
  font-size: x-large;
  color: burlywood;
}

.avatar {
  height: 200px;
  width: 200px;
  object-fit:cover;
  border-radius: 50%;
}

.App-link {
  color: #61dafb;
}

.heroImage {
  height: auto;
  width: 100%;
}
.card-content {
  color: black;
}
.card {
  overflow: hidden;
}

.card-content {
  font-size: medium;
  padding-left: 1em;
  padding-bottom: 1em;
}

.grid {
  display: -webkit-box; /* Not needed if autoprefixing */
  display: -ms-flexbox; /* Not needed if autoprefixing */
  display: flex;
  width: auto;
}
.grid-column {
  padding: 5px; /* gutter size */
  background-clip: padding-box;
}

/* Style your items */
.grid-column > div { /* change div to reference your elements you put in <Masonry> */
  background: white;
  margin:0.5em;
  border-radius: 5px;
  margin-bottom: 30px;
  border: 1px solid #ccc!important;
  transition: 500ms 
  
}
.grid-column > div:hover,div:focus { /* change div to reference your elements you put in <Masonry> */
  box-shadow: 1px 0px 10px #e0e0e0;
  transform: scale(1.05)
  
}
.nav-bar {
  display: grid;
  flex-direction: row;
  background-color: white;
  place-items: center;
  border-bottom: 1px solid #ccc!important;

}

ul.menu {
  display: flex;
  /* padding: 10px; */
}

.menu-item {
  display: block;
  text-decoration: none;
  padding: 10px;
}

a {
  text-decoration: none; 
  font-size: large;
  font-weight: bold;
  color: black;

}
